@media screen and (max-width: 895px) {
  .player-options{
    input{
      margin-top: 2px;
    }
  
  }
  .pill{
    width:100%
  }
}
@media screen and (min-width: 895px) {
  .view-lines-button {
    width: auto;

  }
}
.view-lines-button {
    min-width: 165px;
    height: 50px;
    letter-spacing: 0.5px;
    line-height: 50px;
    padding: 0 35px 0 35px;
    font-size: 15px;
    background-color: #55d622;
    color: black;
    opacity: 90%;
    text-transform: uppercase;
    font-family: 'arial';
    font-weight: bolder;
    border: none;
    cursor: pointer;
    display: flex;
    justify-content: center;
 
    &:hover {
      color: #242424;
      border: 1px solid black;
      opacity: 110%;
    }
    &.google-sign-in{
        background-color: #4285f4;
        &:hover{
            background-color: #357ae8;
            border:none
        }
    }
    &.inverted{
      background-color: white;
      color:black;
      border: 1px solid black;

      &:hover{
        background-color:#242424;
        color:white;
        border:none
      }
    }
  }
  
  
  @media screen and (max-width: 895px) {
    .player-options-top{
      display: flex;
      flex-direction: column;
    }
  }

  @media screen and (max-width: 500px) {

    .view-lines-button {
      width:50%;
      margin-left:15%;
      margin-top:4px;
    }
    .player-options-content{
     background-color: #3b3b3b;
    }
  }
