@media screen and (max-width: 895px) {

    .view-lines-content{
    
        display:flex;
        flex-direction: column;
        background-color: #000000;

        
    }
    .left-view-lines{
        width:100%
    }
    .line-card-holder{
        width:100%
    }
    .bet-slip-container{
        width:50%
    }
    .lineups{
        display: flex;
        flex-direction: column;
        width: "95%";
        margin-left: "2.5%";
    }
  
}
@media screen and (min-width: 895px) {
    .view-lines-content{
        background-color: #000000;
        width:90%;
        margin-left:5%;
        padding:10px;
        display:flex;
        height: 675px;
    }
    .left-view-lines{
        width:60%
    }
    .bet-slip-container{
        width:90%
    }
    .lineups{
        display: flex;
        flex-direction: row;
        width: 40%;
    }
    
}
@media screen and (max-width: 500px) {

.left-top{
    display: flex;
    flex-direction: column;
}
.bet-slip-container{
    width:95%
}
.back-buttons-view-lines{
    display: flex;
        flex-direction: row;
        justify-content: space-evenly;
}
.view-lines-user-team{
    margin-top: 3px;
    width:95%
}
.view-lines-opponent-team{
    margin-top: 3px;
    width:95%
}
}
@media screen and (min-width: 500px) {
    

    .left-top{
        display: flex;
        flex-direction: row;
    }
    .back-buttons-view-lines{
        display: flex;
            flex-direction: column;
    }
    .view-lines-user-team{
        width:50%
    }
    .view-lines-opponent-team{
        width:50%
    }
    }

.view-lines{
   background-color: #d5d5d5;
   height:800px;
}

ul{
    list-style-type: none;
}

.dark{
    background-color: #dadada;
}
.team{
    border-collapse: collapse;
    border-spacing: 0px;
    box-sizing: border-box;
    border-radius: 3px;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    border-color: grey;
        text-align: left;
        width:100%;
      padding:5px;
      background-color: #dadada;
    td{
        padding-left:5px;
    }

    
}
.positions-for-teams-container{
   background-color:  #f9f9fb;
   width:33%
}
.positions-for-teams{
    width:100%;

}





.bottom{
   a {
    text-decoration: none;
    display: inline-block;
    padding: 8px 16px;
  }
  
  .previous:hover {
    background-color: #ddd;
    color: black;
  }
  
  .previous {
    background-color: #f1f1f1;
    color: black;
  }
  
  .next {
    background-color: #04AA6D;
    color: white;
  }
  
  .round {
    border-radius: 50%;
  }
}


.player-projected-score{
float:right;
}

.edit-lineup-button-container{
    text-align: left;
    display: inline-block;
    padding: 8px 16px;
    background-color: #dadada;
    border-radius: 5px;
    margin-bottom:10px;
    &:hover{
        cursor:pointer;
    }

    a{
        text-decoration: none;
        color:black;



    }
}
.click-lines-button{
    background-color: #242424;
    height:30px;
    width:33%;
    color:white;
    text-align: center;
    padding:7px;
    border-right:#121212 solid 1.5px;

    &:hover{
        cursor: pointer;
    }
}

.header-view-lines{
    border-bottom: #242424 solid 1px;
    margin-bottom: 3px;
}
.activated{
    background-color: #ffffff;
    color:black;
}


