}

#roster-container{
  background-color:#fef99b;
  font-family: 'Architects Daughter', cursive;
  font-weight:800;
  font-size: 13px;
}
.drop-player{
  margin-left: 2px;
  margin-top: 2px;

  font-size: 15px;
  color: #c01f5c;
  &:hover{
    cursor: pointer;
    color: darkred;
  }
  
}
td{
  height: 39px;
}
.roster-header{
  display: flex;
  flex-direction: row;
  height:50px;
  border-bottom: 1px solid #dedede;
}
.lines{
  border-left: 1px solid #ffaa9f;
  border-right: 1px solid #ffaa9f;
  width: 3px;
  height: 400px;

}
.roster-row{
  border-bottom: 1px dotted #ccc;

}